/* ---
UI Theme & Style: Minimalist Fintech Dark Mode (View Announcements Page)
Font: Inter (Geometric Sans-Serif)
--- */

/* 1. VARIABLES (Consistent with other pages) */
:root {
  --bg-primary: #111111;
  --bg-card: #1C1C1C;
  --text-primary: #FFFFFF;
  --text-secondary: #A1A1AA;
  --border-color: #333333;
  --error-color: #ef4444;
}

/* 2. BASE LAYOUT */
.announcements-container {
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: 3rem 1.5rem;
  font-family: 'Inter', sans-serif;
  color: var(--text-primary);
  background-color: var(--bg-primary);
  min-height: 100vh;
}

/* 3. HEADER */
.announcements-header {
  text-align: center;
  margin-bottom: 3rem;
}

.announcements-header h1 {
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 0.5rem 0;
}

.announcements-header p {
  font-size: 1.125rem;
  color: var(--text-secondary);
  max-width: 600px;
  margin: 0 auto;
}

/* 4. ANNOUNCEMENTS LIST & ITEMS */
.announcements-content {
  width: 100%;
}

.announcements-list {
  display: grid;
  gap: 1.5rem;
}

.announcement-item {
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  padding: 2rem;
}

.announcement-item-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 1rem 0;
}

.announcement-item-content {
  color: var(--text-secondary);
  line-height: 1.6;
  margin: 0 0 1.5rem 0;
}

.announcement-item-meta {
  text-align: right;
  font-size: 0.875rem;
  color: var(--text-secondary);
  font-style: italic;
  opacity: 0.7;
}

/* 5. STATUS MESSAGES (Loading, Error, Empty) */
.status-message {
  text-align: center;
  font-size: 1.125rem;
  color: var(--text-secondary);
  padding: 3rem;
  background-color: var(--bg-card);
  border: 1px solid var(--border-color);
  border-radius: 16px;
}

.status-message.error {
  color: var(--error-color);
}

/* 6. RESPONSIVE STYLES */
@media (max-width: 768px) {
  .announcements-container {
    padding: 1.5rem 1rem;
  }
  .announcements-header h1 {
    font-size: 2rem;
  }
}
